export default {
	components:{
		
	},
	data() {
		return {
			// 公共模态框
			modalShow: false,
			modalTitle: '',
			modalInput: '',
			modalInput2: '',
			modalTip: '请输入您的信息~',
			clearIconShow: false,
			clearIconShow2: false,
			clearIconShow3: false,
			remark: '',
			remarkPlaceholder: '备注~'
		};
	},
	methods: {
		navTo(url) {
			uni.navigateTo({
				url
			})
		},
		hideModal(){
			this.modalShow = false
			this.modalInput = ''
			this.modalInput2 = ''
			this.remark = ''
		},
		modalConfim(e){
			if(this.modalTitle=='发现新竞赛') {
				if(this.modalInput == '') {
					this.$api.msg("请填写链接~")
					return
				}
				this.found(0)
			}
			if(this.modalTitle=='发现新活动') {
				if(this.modalInput == '') {
					this.$api.msg("请填写链接~")
					return
				}
				this.found(1)
			}
			if(this.modalTitle=='申请管理员') {
				if(this.modalInput2=='' || this.remark=='') {
					this.$api.msg("请完善信息~")
					return
				}
				this.applyAdmin()
			}
		},
		async found(type) {
			uni.showLoading({
				title:'加载中...'
			})
			const res = await this.$api.request({
				url: '/found/found',
				contentType: "application/json",
				method: 'POST',
				data: {
					contact: this.modalInput2,
					url: this.modalInput,
					remark: this.remark,
					type: type
				}
			})
			uni.hideLoading()
			if(res.status == 200) {
				this.hideModal()
				this.$api.msg("感谢您的反馈~")
			} else {
				this.$api.msg("服务器正忙，请直接联系管理员，感谢您的反馈~")
			}
		},
		async applyAdmin() {
			// uni.showLoading({
			// 	title:'加载中...'
			// })
			const res = await this.$api.request({
				url: '/admin/apply',
				contentType: "application/json",
				method: 'POST',
				data: {
					contact: this.modalInput2,
					remark: this.remark
				}
			})
			// uni.hideLoading()
			if(res.status == 200) {
				this.hideModal()
				this.$api.msg("感谢您的反馈~")
			} else {
				this.$api.msg("服务器正忙，请直接联系管理员，感谢您的反馈~")
			}
		},
		modalInputing(){
			if(this.modalInput.length > 0) {
				this.clearIconShow = true
			}else {
				this.clearIconShow = false
			}
		},
		modalInputing2(){
			if(this.modalInput2.length > 0) {
				this.clearIconShow2 = true
			}else {
				this.clearIconShow2 = false
			}
		},
		modalInputing3(){
			if(this.remark.length > 0) {
				this.clearIconShow3 = true
			}else {
				this.clearIconShow3 = false
			}
		},
		showPublicModal(e){
			if(e == "newAdmin") {
				this.modalTitle='申请管理员'
				this.remarkPlaceholder = '申请理由'
			}
			if(e == "newGame") {
				this.modalTitle='发现新竞赛'
				this.modalTip = '请输入竞赛链接~'
				this.remarkPlaceholder = '备注'
			}
			if(e == "newActivity") {
				this.modalTitle='发现新活动'
				this.modalTip = '请输入活动链接~'
				this.remarkPlaceholder = '备注'
			}
			this.modalShow = true
		},
		clearIcon(){
			this.modalInput = ''
		},
		clearIcon2(){
			this.modalInput2 = ''
		},
		clearIcon3(){
			this.remark = ''
		},
	}
}
